<template>
  <div style="margin-top: 44px;">
    <div style="margin: 10px; background: #FFFFFF; padding-bottom: 10px;">
      <van-field
        readonly
        v-model="target_times"
        label=""
        type="text"
      />
      <van-field
        v-model="type"
        label=""
        readonly
      />
      <van-field
        v-model="price"
        label=""
        readonly
      />
      <van-field
        readonly
        v-model="total_cost"
        autosize
        label=""
      />
      <div style="margin: 16px;">
        <van-button block type="info" @click="copy">
          
        </van-button>
      </div>
    </div>
    <div style="margin: 10px;"></div>
    <div style="margin: 10px; background: #FFFFFF; padding-bottom: 10px;">
      <van-uploader upload-icon="camera_full"
                     accept="image/png, image/jpeg"
                     :max-size="3 * 1024 * 1024"
                     :after-read="afterRead"
                      v-model="fileList"
                      multiple
                     :max-count="3"
                     style="margin: 10px 0 0 10px;"
      />
      <div style="margin: 16px;">
        <van-button block type="info" @click="submitTask">
          
        </van-button>
      </div>
    </div>
  </div>
</template>
<script>
import { Form, Field, Picker, Popup, Button, Uploader, Toast, Dialog } from 'vant'
import field from '@/components/field/'
import fieldGroup from '@/components/field-group/'
export default {
  name: 'pickTask',
  data () {
    return {
      url: '111',
      target_times: '',
      total_cost: '',
      price: '',
      type: '',
      num: 0,
      complete_price: '',
      fileList: []
    }
  },
  created () {
  },
  methods: {
    afterRead (file) {
      console.log(file)
    },
    copy () {
      let onInput = document.createElement('input')
      onInput.value = this.url
      document.body.appendChild(onInput)
      onInput.select()
      document.execCommand('Copy')
      Toast.success('')
      onInput.remove()
    },
    submitTask () {
      Dialog.alert({
        title: '',
        message: ''
      }).then(() => {
        this.$router.replace({
          name: 'getTask',
          query: {
            activeIndex: 0
          }
        })
      })
    }
  },
  components: {
    [Form.name]: Form,
    [Field.name]: Field,
    [field.name]: field,
    [fieldGroup.name]: fieldGroup,
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [Button.name]: Button,
    [Uploader.name]: Uploader
  }
}
</script>

<style scoped>

</style>



